.news-list {
    box-sizing: border-box;
    min-height: 282px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #F5F6F7;
    padding-top: 80px;
    padding-bottom: 80px;
}

.news-list_type_saved {
    padding: 62px 102px;
}

.news-list__title {
    align-self: flex-start;
    margin: 0;
    margin-bottom: 64px;
    font-family: 'Roboto Slab';
    font-style: normal;
    font-weight: 400;
    font-size: 40px;
    line-height: 1.15;
}

.news-list__title_type_not-found {
    align-self: center;
    font-size: 26px;
}

.news-list__container {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 16px;
}

.news-list__show-more {
    display: block;
    background-color: #fff;
    border: none;
    border-radius: 80px;
    padding: 20px 98px;
    margin-top: 64px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 1.33;
}

.news-list__show-mor_not-visible {
    display: none;
}

.news-list__show-more:hover {
    background-color: #E8E8E8;
}

.news-list__not-found-icon {
    background-image: url(/news-explorer-frontend/static/media/not-found-icon.cc9bdc2a.svg);
    min-height: 96px;
    min-width: 96px;
    margin-bottom: 24px;
}

.news-list__not-found-text {
    max-width: 250px;
    text-align: center;
    color: #B6BCBF;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.33;
    margin: 0;
    margin-top: 16px;
}

@media screen and (max-width:950px){
    .news-list {
        padding-top: 40px;
        padding-bottom: 40px;
    }
    
    .news-list_type_saved {
        padding: 32px 40px;
    }
    
    .news-list__title {
        margin-bottom: 32px;
        font-size: 30px;
        line-height: 1.13;
    }
    
    .news-list__title_type_not-found {
        align-self: center;
        font-size: 26px;
    }
    
    .news-list__container {
        grid-gap: 8px;
    }
    
    .news-list__show-more {
        padding: 16px 74px;
        margin-top: 32px;
    }
}

@media screen and (max-width:620px){
    .news-list__container {
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 10px;
    }

    .news-list_type_saved {
        padding: 24px 16px 13px;
    }
}

@media screen and (max-width:375px){
    .news-list__container {
        grid-template-columns: 1fr;
        grid-gap: 10px;
    }
}
.preloader {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.preloader__circle {
	display: block;
	width: 74px;
	height: 74px;
	border: 4px solid #444;
	border-bottom-color: #888;
	
	border-radius: 50%;
	
	-webkit-animation: spin .75s infinite linear;
	
	        animation: spin .75s infinite linear;
}

.preloader__text {
    color: #B6BCBF;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.33;
    margin: 0;
    margin-top: 24px;
}

@-webkit-keyframes spin {
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

@keyframes spin {
	100% {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
.news-card {
    position: relative;
    background-color: #fff;
    border-radius: 16px;
    overflow: hidden;
}

.news-card__image {
    min-height: 272px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
}

.news-card__bubble {
    position: absolute;
    box-sizing: border-box;
    top: 24px;
    min-width: 40px;
    min-height: 40px;
    background-color: #fff;
}

.news-card__keyword {
    left: 24px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 1.71;
    text-align: center;
    padding: 0 22px;
    margin: 0;
}

.news-card__button {
    right: 24px;
    border: none;
    border-radius: 8px;
    background-repeat: no-repeat;
    background-position: center;
    padding: none;
    cursor: pointer;
}

.news-card__button-hover-text {
    right: 69px;
    border-radius: 10px;
    padding: 11px 20px;
    margin: 0;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 1.16;
    display: flex;
    align-items: center;
    text-align: center;
    display: none;
}

.news-card__button:hover + .news-card__button-hover-text {
    display: flex;
}

.news-card__button_type_save {
    background-image: url(/news-explorer-frontend/static/media/grey-bookmark-icon.5efd35d2.svg);
}

.news-card__button_type_save:hover {
    background-image: url(/news-explorer-frontend/static/media/black-bookmark-icon.240acee0.svg);
}

.news-card__button_type_saved {
    background-image: url(/news-explorer-frontend/static/media/marked-bookmark-icon.f92b7c1a.svg);
}

.news-card__button_type_saved:hover {
    background-image: url(/news-explorer-frontend/static/media/marked-bookmark-icon.f92b7c1a.svg);
}

.news-card__button_type_remove {
    background-image: url(/news-explorer-frontend/static/media/grey-bin-icon.33bb9dfd.svg);
}

.news-card__button_type_remove:hover {
    background-image: url(/news-explorer-frontend/static/media/black-bin-icon.85bdc9b1.svg);
}

.new-card__text-container {
    padding: 20px 24px 24px;
}

.new-card__date {
    color: #B6BCBF;
    font-family: 'Source Sans Pro';
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.33;
    margin: 0;
